import React, { Fragment } from "react";
import { Link } from "react-router-dom";

import PropTypes from "prop-types";
import { connect } from "react-redux";
import Moment from "react-moment";
import { addLike, removeLike, removePost } from "../../actions/post";

const PostItem = ({
  post: { _id, user, name, avatar, text, date, likes, comments },
  auth,
  removePost,
  addLike,
  removeLike,
  showActions,
}) => {
  return (
    <div className="post bg-white my-1">
      <div>
        <a href={`/profile/${user}`}>
          <img className="round-img my-1" src={avatar} alt="" />
          <h4>{name}</h4>
        </a>
      </div>

      <div className="my-1">
        <p className="my-1">{text}</p>
        <p className="post-date">
          Posted on <Moment format="YYYY/MM/DD">{date}</Moment>
        </p>

        {showActions && (
          <Fragment>
            <button className="btn" onClick={e => addLike(_id)}>
              <i className="fas fa-thumbs-up"></i>{" "}
              <span>{likes.length > 0 && <span>{likes.length}</span>}</span>
            </button>
            <button className="btn" onClick={e => removeLike(_id)}>
              <i className="fas fa-thumbs-down"></i>
            </button>
            <Link to={`/posts/${_id}`} className="btn btn-primary">
              Discussion{" "}
              {comments.length > 0 && (
                <span className="comment-count">{comments.length}</span>
              )}
            </Link>
            {!auth.loading && user === auth.user._id && (
              <button
                type="button"
                className="btn btn-danger"
                onClick={e => removePost(_id)}
              >
                <i className="fas fa-times" />
              </button>
            )}
          </Fragment>
        )}
      </div>
    </div>
  );
};

PostItem.defaultProps = {
  showActions: true,
};

PostItem.propTypes = {
  post: PropTypes.object.isRequired,
  auth: PropTypes.object.isRequired,
  addLike: PropTypes.func.isRequired,
  removeLike: PropTypes.func.isRequired,
  removePost: PropTypes.func.isRequired,
};

const mapStateToProps = state => ({
  auth: state.auth,
});

export default connect(mapStateToProps, { addLike, removeLike, removePost })(
  PostItem
);
