import {
    GET_PROFILE,
    GET_PROFILES,
    PROFILE_ERROR,
    UPDATE_PROFILE,
    CLEAR_PROFILE,
    ACCOUNT_DELETED,
} from "../actions/types";

const initialState = {
    profile: null,
    profiles: [],
    loading: true,
    error: {},
};

export default function profile(state = initialState, action) {
    const { type, payload } = action;

    switch (type) {
        case GET_PROFILE:
        case UPDATE_PROFILE:
            return {
                ...state,
                loading: false,
                profile: payload,
            };
        case GET_PROFILES:
            return {
                ...state,
                loading: false,
                profiles: payload,
            };
        case PROFILE_ERROR:
            return {
                ...state,
                loading: false,
                error: payload,
            };
        case CLEAR_PROFILE:
        case ACCOUNT_DELETED:
            return {
                ...state,
                loading: false,
                profile: null,
            };
        default:
            return state;
    }
}
