import React, { Fragment, useEffect } from "react";
import { Link } from "react-router-dom";

import { connect } from "react-redux";
import { getProfileById } from "../../actions/profile";
import PropTypes from "prop-types";

import ProfileTop from "./ProfileTop";
import Spinner from "../layout/Spinner";
import ProfileAbout from "./ProfileAbout";
import ProfileExperience from "./ProfileExperience";
import ProfileEducation from "./ProfileEducation";

const Profile = ({
  match,
  getProfileById,
  profile: { profile, loading },
  auth,
}) => {
  useEffect(() => {
    getProfileById(match.params.id);
  }, [match.params.id, getProfileById]);
  return (
    <Fragment>
      {profile === null || loading ? (
        <Spinner />
      ) : (
        <Fragment>
          {" "}
          <Link to="/profiles" class="btn">
            Back To Profiles
          </Link>
          {auth.isAuthenticated &&
            !loading &&
            auth.user._id === profile.user._id && (
              <Fragment>
                <Link to="/edit-profile" class="btn">
                  Edit Profile
                </Link>
              </Fragment>
            )}
          <div class="profile-grid my-1">
            <ProfileTop profile={profile} />
            <ProfileAbout profile={profile} />

            {/* <!-- Experience --> */}
            <div class="profile-exp bg-white p-2">
              <h2 class="text-primary">Experiences</h2>
              {profile.experience.length > 0 ? (
                profile.experience.map(experience => (
                  <ProfileExperience
                    experience={experience}
                    key={experience._id}
                  />
                ))
              ) : (
                <h4>No Experience Found...</h4>
              )}
            </div>

            {/* <!-- Education --> */}
            <div class="profile-edu bg-white p-2">
              <h2 class="text-primary">Education</h2>
              {profile.education.length > 0 ? (
                profile.education.map(education => (
                  <ProfileEducation education={education} key={education._id} />
                ))
              ) : (
                <h4>No Education Found...</h4>
              )}
            </div>
          </div>
        </Fragment>
      )}
    </Fragment>
  );
};

Profile.propTypes = {
  getProfileById: PropTypes.func.isRequired,
  profile: PropTypes.object.isRequired,
  auth: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
  profile: state.profile,
  auth: state.auth,
});

export default connect(mapStateToProps, { getProfileById })(Profile);
